import React, { useState } from 'react';
import { InputText } from 'primereact/inputtext';
import { Password } from 'primereact/password';
import { Button } from 'primereact/button';
import axios from 'axios';
import {Message} from "primereact/message";

const LoginPage = ({ onLogin }) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');

    const handleLoginClick = async () => {
        try {
            const response = await axios.post('/lab4/api/auth/login', {
                login: username,
                password: password
            });
            if (response.status === 200) {
                onLogin(username);
            }
        } catch (error) {
            alert("   !       .");
        }
    };

    const handleRegisterClick = async () => {
        try {
            await axios.post('/lab4/api/auth/register', {
                login: username,
                password: password
            });
            alert(" !   .");
        } catch (error) {
            alert(" : ,  !!.");
        }
    };

    const validate = () => {
        if (!username.trim() || !password.trim()) {
            setError("  !");
            return false;
        }
        return true;
    };

    return (
        <div className="flex flex-column align-items-center justify-content-center" style={{ minHeight: '100vh' }}>

            {/*    */}
            <div className="card shadow-2 mb-4 p-4 w-full md:w-30rem text-center bg-white text-black border-round-xl">
                <h2 className="m-0">  </h2>
                <p className="mt-2 mb-0">: P3222 | : 74931</p>
            </div>

            {/*   */}
            <div className="card shadow-4 p-5 w-full md:w-30rem bg-white border-round-xl">
                <h3 className="text-center mb-4 text-900"></h3>

                {error && <Message severity="error" text={error} className="w-full mb-4" />}

                <div className="p-fluid">
                    <div className="field mb-4">
                        <label htmlFor="username" className="font-bold block mb-2"></label>
                            <InputText
                                id="username"
                                value={username}
                                onChange={(e) => setUsername(e.target.value)}
                                placeholder=" "
                            />
                    </div>

                    <div className="field mb-4">
                        <label htmlFor="password" className="font-bold block mb-2"></label>
                        <Password
                            id="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            feedback={false}
                            toggleMask
                            placeholder=" "
                        />
                    </div>

                    <div className="flex flex-column gap-2 mt-2">
                        <Button label="" icon="pi pi-sign-in" onClick={handleLoginClick} className="p-button-outlined" />
                        <Button label="" icon="pi pi-user-plus" onClick={handleRegisterClick} className="p-button-outlined p-button-secondary" />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default LoginPage;